.odc-task-list-node {
  border: 1px solid var(--pf-global--BorderColor--light-100);
}

.odc-task-list-node__label {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.odc-task-list-node__trigger-background {
  background: var(--pf-global--BackgroundColor--light-100);
}

.odc-task-list-node__trigger {
  padding: var(--pf-global--spacer--xs) var(--pf-global--spacer--md);
  width: 100%;
}

.odc-task-list-node__list-items {
  max-height: 350px;
  overflow: auto;
}

.odc-task-list-node__divider {
  border-color: var(--pf-global--BorderColor--100);
  margin: 0;
}

